import React, { Component } from 'react'
import '../../styles/App.css'

import DbHelper from '../_utils/DbHelper.js'


import { RiSendPlane2Line } from 'react-icons/ri'

class ChatPage extends Component {

    constructor(props) {
        super(props);
        this.state = {

        };

        this.dbHelper = new DbHelper()

    }

    componentDidMount() {
        this.dbHelper.getChat(this.setChat)

        // Scroll to bottom of chat
        const objDiv = document.getElementById('Chat-Scroll');
        objDiv.scrollTop = objDiv.scrollHeight;
    }

    componentDidUpdate() {
        // Scroll to bottom of chat
        const objDiv = document.getElementById('Chat-Scroll');
        objDiv.scrollTop = objDiv.scrollHeight;
    }

    setChat = (messages) => {
        this.setState({ messages: messages })
    }

    sendMessage = () => {
        if (this.state.textToSend) {
            this.dbHelper.postMessage(this.state.textToSend, this.props.user.username, Math.floor(new Date().getTime() / 1000))

            const objDiv = document.getElementById('Chat-Message');
            objDiv.value = '';

            this.setState({ textToSend: '' })
        }
    }

    textAreaChange = (e) => {
        console.log('val', e)
        this.setState({
            textToSend: e.target.value
        });
    }

    handleTextEnter = (e) => {
        if (e.key === 'Enter' && this.state.textToSend) {
            this.sendMessage()
        }
    }

    render() {


        if (this.state.messages) {
            var dayNames = ['Sun', 'Mon', 'Tues', 'Weds', 'Thurs', 'Fri', 'Sat']

            var renderMessages = Object.entries(this.state.messages).map((message) => {
                var messageTime = new Date(message[1].time * 1000)
                return (
                    <div key={message[0]} style={{ display: 'flex', flexDirection: 'column', margin: '2px', padding: '4px', maxWidth: '95%', background: '#394c68', }}>
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                            <div style={{ fontSize: '14px', textAlign: 'left', paddingLeft: '8px', color: '#dce0da' }}>
                                {message[1].sender}
                            </div>
                            <div style={{ fontSize: '12px', fontStyle: 'italic', textAlign: 'left', paddingLeft: '8px', color: '#dce0da' }}>
                                {dayNames[messageTime.getDay()]} {messageTime.getDate()}, {messageTime.getHours()}:{messageTime.getMinutes() < 10 ? '0' : ''}{messageTime.getMinutes()}
                            </div>
                        </div>
                        <div style={{ fontSize: '18px', textAlign: 'left', paddingLeft: '12px' }}>
                            {message[1].text}
                        </div>
                    </div>
                )
            })
        }

        return (
            <div style={{ display: 'flex', flexDirection: 'column', minWidth: '100%', maxWidth: '100%', maxHeight: '100%', minHeight: '100%', background: '#0a3a27' }}>

                <div id="Chat-Scroll" style={{ flex: 1, maxHeight: '100%', minHeight: '100%', overflowY: 'auto', overflowX: 'hidden' }}>
                    {renderMessages}
                </div>

                <div style={{ height: '60px', maxHeight: '60px', background: '#26483b', display: 'flex', flexDirection: 'row' }}>
                    <textarea id="Chat-Message" onChange={this.textAreaChange} onKeyDown={this.handleTextEnter} style={{ fontSize: '22px', flex: 1, width: '80px' }} />
                    <div style={{ minwidth: '40px' }}>
                        <RiSendPlane2Line onClick={this.sendMessage} className="Chat-Send-Button" style={{ fontSize: '60px', width: '60px', minWidth: '60px', height: '100%', background: '#368039', cursor: 'pointer' }} />
                    </div>
                </div>
            </div>
        );
    }
}

export default ChatPage;
