import React, { Component } from 'react';

import { WiThermometer, WiHumidity, WiHurricane, WiCloudUp, WiThermometerExterior, WiBarometer } from 'react-icons/wi';
import co2svg from '../../assets/co2svg.svg'
import tvocSvg from '../../assets/tvoc-svg.svg'

class DisplayFunctions {

    constructor() {
    }


    ////////////////////
    // Sensor Type Icon
    ///////////////////
    displaySensorTypeIcon = (sensorType) => {
        if (sensorType === "airTemp") {
            return (<WiThermometer style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "waterTemp") {
            return (<WiThermometerExterior style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "humidity") {
            return (<WiHumidity style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "fan") {
            return (<WiHurricane style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "humidifier") {
            return (<WiCloudUp style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "pressure") {
            return (<WiBarometer style={{ fontSize: '20px', color: '#fff', padding: '1px' }} />)
        } else if (sensorType === "co2") {
            return (<img src={co2svg} alt="CO2 Icon" style={{ maxHeight: '70%', maxWidth: '20px', color: '#fff', padding: '2px' }} />)
        } else if (sensorType === "tvoc") {
            return (<img src={tvocSvg} alt="TVoC Icon" style={{ maxHeight: '70%', maxWidth: '20px', color: '#fff', padding: '2px' }} />)
        } else {
            return (<div />)
        }
    }

    /////////////////////
    // Grow Active? Color
    /////////////////////
    returnActiveIndicatorColor = (processedData) => {
        if (processedData && processedData[processedData.length - 1]) {
            var now = new Date().getTime();
            var difference = now - processedData[processedData.length - 1].time

            if (difference > 480 * 60 * 1000) {
                return "#212521"
            } else if (difference > 5 * 60 * 1000) {
                return "#fa360a"
            } else if (difference > 60 * 1000) {
                return "#facb23"
            } else if (difference < 60 * 1000) {
                return "#27d927"
            } else {
                return "#000"
            }
        }
    }

    //////////////////////////////
    // Sensor Current Status Color
    //////////////////////////////
    returnCurrentIndicatorColor = (curSensor, liveData) => {
        if (liveData && curSensor && (curSensor._mean || curSensor._mean === 0) && (curSensor._deviation || curSensor._deviation === 0)) {
            if (((curSensor._mean + (curSensor._deviation * 2)) < liveData) || ((curSensor._mean - (curSensor._deviation * 2)) > liveData)) {
                return '#FF0000' // BAD
            } else if (((curSensor._mean + (curSensor._deviation)) < liveData) || ((curSensor._mean - (curSensor._deviation)) > liveData)) {
                return '#ded954' // WARN
            } else {
                return '#38c538' // GOOD
            }
        } else {
            return '#FFF'
        }
    }

    ///////////////////////
    // Sensor Health Color
    ///////////////////////
    returnHealthBar = (warnCount, dangerCount) => {
        var harmonyWarnFlex = 0
        var harmonyDangerFlex = 0
        var harmonyGoodFlex = 100
        // EXAGERATE WARNINGS *2 / *10
        if (warnCount) {
            harmonyWarnFlex = warnCount * 2
        }
        if (dangerCount) {
            harmonyDangerFlex = dangerCount * 10
        }
        if (harmonyDangerFlex > 100) {
            harmonyWarnFlex = 0
        }
        harmonyGoodFlex = harmonyGoodFlex - harmonyWarnFlex - harmonyDangerFlex

        return (
            <div style={{ display: 'flex', flexDirection: 'column', height: "100%", width: "4px", maxWidth: "4px", minWidth: '4px' }}>
                <div style={{ flex: harmonyDangerFlex, background: '#FF0000' }} />
                <div style={{ flex: harmonyWarnFlex, background: '#fcba03' }} />
                <div style={{ flex: harmonyGoodFlex, background: '#38c538' }} />
            </div>
        )
    }






}

export default DisplayFunctions;